





























































































































































































.preview-box {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  background: #000;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);

  .scale-box {
    position: absolute;
    right: 0;
    top: -90px;
  }
  .device-change-box {
    position: absolute;
    left: 200px;
    top: -60px;
  }
}

#campaign-layout-1 {
  flex-shrink: 0;
  position: relative !important;
  left: 0 !important;
  top: 0 !important;
  opacity: 1;
  z-index: 1;
  margin-left: 16px;
  margin-top: 16px;
  transform-origin: left top;
}

#campaignStyle {
  opacity: 0;
  pointer-events: none;
  display: none;
}

.tablet-campaign-wrap .campaign-layout-1 {
  width: 614px !important;
  height: 360px !important;
}

.mobile-campaign-wrap .campaign-layout-1 {
  width: 320px !important;
  height:  auto !important;
  .right-box {
    margin-top: 200px !important;
  }
}
