#config-area {
  overflow: auto;
  overflow-x: hidden;
  border: 1px solid #f20;
  height: 100%;
}

.config-box {
  height: 80vh;
}

.campaign-alert {
  position: fixed !important;
  right: 32px;
  top: 32px;
}

.config-item {
  box-shadow: 0 0 2px rgba(#000, 0.175);
  padding-left: 8px;
  margin-bottom: 18px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;

  &.area {
    border: 1px solid #ddd;
    padding-right: 8px;
    padding-bottom: 8px;
  }

  > .material-icons {
    margin-left: 16px;
    cursor: pointer;

    &:hover {
      color: #333;
    }
  }
}

.layout-campaign {
  width: 120px;
  display: block;
  margin-left: 8px;
}

.codemirror-item {
  width: 100%;
}
